
.about-container {
  padding-top: 60px;
  padding-left: 21px;
  padding-right: 21px;
}
.about__list {
  text-align: center;
}
.about-label {
  margin-bottom: 12px;

  text-align: center;

  font-weight: 700;
  font-size: var(--mobile-label-font-size);
  line-height: 1.22;
  color: var(--primary-text-color);
  text-transform: uppercase;
}

.about-title {
  width: 100%;

  text-align: center;
  margin-bottom: 32px;
  font-family: Seymour One;
  font-weight: 400;
  font-size: var(--mobile-title-font-size);
  line-height: 1.26;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--primary-text-color);
}

.about__img {
  margin-left: auto;
  margin-right: auto;
  
}

.about__item {
  width: 100%;
  text-align: center;
  margin-bottom: 40px;
}

@media screen and (min-width: 769px) {
  
  .about {
      padding-top: 57px;
  
    }
  
    .about-container {
      padding-right: 105px;
    }

.about__list {
  display: flex;
  gap: 100px;
  align-items: center;
}

.about-description__text {
  width: 100%;

  font-size: 16px;

  font-weight: 400;
  font-size: var(--mobile-secondary-font-size);
  line-height: 1.76;
  letter-spacing: 0.04em;

  color: var(--primary-text-color);

 
}


}



