.modal_height {
    height: 674px;
    padding: 28px;
}

.modal__text {
    margin-bottom: 24px;

    font-weight: 400;
    font-size: var(--mobile-primary-font-size);
    line-height: 1.4;
    letter-spacing: var(--third-letter-spacing);
    color: var(--secondary-text-color);
}

.modal-form__textarea {
    margin-bottom: 24px;
}

.modal-form__input {
    margin-bottom: 12px;
    padding: 8px 12px;
    width: 224px;
    height: 32px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid var(--primary-bg-color);
    border-radius: 10px;

    transition: border-color var(--transition-dur-and-func);

    &:focus {
        border-color: var(--accent-color);
        outline: none;
    }

    &::placeholder {
        font-weight: 400;
        font-size: var(--mobile-secondary-font-size);
        line-height: 1.4;
        color: var(--secondary-text-color);
    }
}

.modal-form__message {
    display: block;
    padding: 8px 12px;
    width: 224px;
    height: 64px;
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid var(--primary-bg-color);
    border-radius: 10px;
    resize: none;

    transition: border-color var(--transition-dur-and-func);

    &:focus {
        border-color: var(--accent-color);
        outline: none;
    }
    
    &::placeholder {
        font-weight: 400;
        font-size: var(--mobile-secondary-font-size);
        line-height: 1.4;
        color: var(--secondary-text-color);
    }
}

@media screen and (min-width: 768px) {
    .modal_height {
        height: 668px;
        padding: 32px 60px;
    }

    .modal__title_width {
        margin-left: auto;
        margin-right: auto;
        width: 322px;
    }
    
    .modal__text {
        width: 448px;
        font-size: 15px;
    }

    .modal-form {
        margin-right: auto;
        margin-left: auto;
    }
    
    .modal-form__textarea {
        margin-bottom: 32px;
    }
    
    .modal-form__input {
        margin-bottom: 16px;
        padding: 11px 16px;
        width: 368px;
        height: 42px;
        background: rgba(248, 210, 78, 0.1);
        border: 1px solid var(--primary-bg-color);
        border-radius: 10px;
    
        &::placeholder {
            font-size: var(--mobile-primary-font-size);  
        }
    }
    
    .modal-form__message {
        display: block;
        padding: 11px 16px;
        width: 368px;
        height: 84px;
    
        &::placeholder {
            font-size: var(--mobile-primary-font-size);
        }
    }
}