*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-text-color: #fcfcfc;
  --secondary-text-color: #131313;
  --third-text-color: #d6936d;
  --accent-color: #f8d24e;
  --time-text-color: #ababab;
  --tel-text-color: #fff;
  --copyright-text-color: #7a7a7a;
  --valid-form-border-color: #2e8b57;
  --invalid-form-border-color: #dc143c;
  --primary-bg-color: #84a178;
  --cream-bg-color: #aec6a5;
  --coffee-bg-color: #efd478;
  --milkshakes-bg-color: #d6936d;
  --input-bg-color: rgba(248, 210, 78, .1);
  --icon-advantages-color: #000;
  --hover-focus-color: #0a8817;
  --disabled-color: #ababab;
  --modal-bg-yellow-07: rgba(248, 210, 78, .7);
  --modal-bg-green-07: rgba(156, 190, 143, .7);
  --modal-bg-brown-07: rgba(213, 117, 63, .7);
  --modal-bg-yellow-1: #f8d24e;
  --modal-bg-green-1: #9cbe8f;
  --modal-bg-brown-1: #d5753f;
  --border-bg-color-label-green: #96af8c;
  --border-bg-color-label-yellow: #efd478;
  --border-bg-color-label-brown: #d6936d;
  --border-color-modal: #f8d24e;
  --border-color-input: #84a178;
  --mobile-primary-font-size: 14px;
  --mobile-secondary-font-size: 12px;
  --mobile-hero-font-size: 26px;
  --mobile-link-font-size: 8px;
  --mobile-label-font-size: 18px;
  --mobile-title-font-size: 30px;
  --mobile-products-font-size: 24px;
  --mobile-number-font-size: 36px;
  --mobile-customer-font-size: 20px;
  --mobile-footer-font-size: 34px;
  --modal-buy-font-size: 16px;
  --mobile-menu-font-size: 32px;
  --mobile-ingred-font-size: 10px;
  --tablet-label-font-size: 22px;
  --tablet-number-font-size: 28px;
  --tablet-advant-font-size: 48px;
  --desktop-hero-font-size: 38px;
  --desktop-number-font-size: 46px;
  --desktop-advant-font-size: 58px;
  --primary-letter-spacing: .04em;
  --secondary-letter-spacing: .06em;
  --third-letter-spacing: .02em;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: translate(0%, -50%);
}

body {
  background-color: var(--primary-bg-color);
  color: var(--primary-text-color);
  font-family: Montserrat, sans-serif;
  font-size: var(--mobile-primary-font-size);
  letter-spacing: var(--primary-letter-spacing);
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

button {
  cursor: pointer;
}

button, input[type="“submit”"], input[type="“reset”"] {
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  background: none;
  border: none;
  padding: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 481px) and (max-width: 768px) {
  .container {
    min-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.products-button {
  text-align: center;
  color: var(--secondary-text-color);
  border: 2px solid var(--accent-color);
  cursor: pointer;
  transition: border-color var(--transition-dur-and-func);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  align-items: center;
  padding: 7px 12px;
  font-family: inherit;
  font-size: 8px;
  font-weight: 600;
  line-height: 1.2;
  display: flex;
}

.products-button:hover {
  border-color: var(--hover-focus-color);
}

.products-button:focus {
  color: var(--primary-text-color);
  border-color: var(--hover-focus-color);
}

.products-button:disabled {
  border-color: var(--disabled-color);
}

@media screen and (min-width: 768px) {
  .products-button {
    border-radius: 10px;
    padding: 12px 34px;
    font-size: 14px;
    line-height: 1.29;
  }
}

.about-button {
  text-align: center;
  color: var(--accent-color);
  cursor: pointer;
  transition: color var(--transition-dur-and-func);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 8px;
  align-items: center;
  padding: 7px 10px;
  font-family: inherit;
  font-size: 8px;
  font-weight: 600;
  line-height: 1.2;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  display: flex;
}

.about-button:hover {
  color: var(--hover-focus-color);
}

.about-button:focus {
  color: var(--primary-text-color);
}

.about-button:disabled {
  color: var(--disabled-color);
}

@media screen and (min-width: 768px) {
  .about-button {
    border-radius: 10px;
    padding: 12px 22px;
    font-size: 14px;
    line-height: 1.21;
  }
}

.read-button {
  text-align: center;
  color: var(--secondary-text-color);
  background-color: var(--accent-color);
  cursor: pointer;
  transition: background-color var(--transition-dur-and-func);
  border: none;
  border-radius: 10px;
  align-items: center;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
}

.read-button:hover {
  background-color: var(--hover-focus-color);
}

.read-button:focus {
  color: var(--primary-text-color);
  background-color: var(--hover-focus-color);
}

.read-button:disabled {
  background-color: var(--disabled-color);
}

.button-icon {
  width: 12px;
  height: 12px;
  fill: currentColor;
  margin-left: 5px;
}

.franchise-button {
  text-align: center;
  color: var(--secondary-text-color);
  border: 2px solid var(--accent-color);
  cursor: pointer;
  transition: border-color var(--transition-dur-and-func);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  align-items: center;
  padding: 12px 46px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  display: flex;
}

.franchise-button:hover {
  border-color: var(--hover-focus-color);
}

.franchise-button:focus {
  color: var(--primary-text-color);
  border-color: var(--hover-focus-color);
}

.franchise-button:disabled {
  border-color: var(--disabled-color);
}

.submit-button {
  text-align: center;
  color: var(--secondary-text-color);
  background-color: var(--accent-color);
  cursor: pointer;
  transition: background-color var(--transition-dur-and-func);
  border: none;
  border-radius: 10px;
  align-items: center;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
  display: flex;
}

.submit-button:hover {
  background-color: var(--hover-focus-color);
}

.submit-button:focus {
  color: var(--primary-text-color);
  background-color: var(--hover-focus-color);
}

.submit-button:disabled {
  background-color: var(--disabled-color);
}

@media screen and (min-width: 768px) {
  .submit-button {
    font-size: 14px;
    line-height: 1.21;
  }
}

.arrow-button {
  text-align: center;
  color: var(--secondary-text-color);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 10px;
  align-items: center;
  padding: 10px;
  display: flex;
}

.arrow-button-icon {
  width: 40px;
  height: 14px;
  transition: width var(--transition-dur-and-func);
}

.arrow-button:hover .arrow-button-icon {
  width: 50px;
}

.arrow-button:focus .arrow-button-icon {
  width: 50px;
  stroke: var(--secondary-text-color);
  stroke-width: 1px;
}

@media screen and (max-width: 768px) {
  .mob-buttons {
    justify-content: flex-end;
    align-items: center;
    gap: 32px;
    display: flex;
  }
}

.menu-close-btn {
  fill: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  display: inline-flex;
  position: absolute;
  top: 32px;
  right: 32px;
}

.menu-close-btn:hover {
  fill: var(--secondary-text-color);
}

.mob-menu {
  background-color: var(--primary-bg-color);
  z-index: 110;
  width: 100%;
  height: 100%;
  padding-top: 140px;
  transition-duration: .85s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(0)scale(1);
}

.mob-is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: var(--secondary-text-color);
  border-radius: 100%;
  transform: translate(100%, -100%)scale(.5);
}

.mob-menu-wrap {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  column-gap: 40px;
  display: flex;
}

.mob-logo {
  position: absolute;
  top: 20px;
  left: 20px;
}

.mob-menu-nav {
  text-align: center;
  margin: 0;
}

.mob-menu__list {
  flex-direction: column;
  column-gap: 47px;
  display: flex;
}

.mob-menu__link {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.2;
}

@media only screen and (min-width: 480px) {
  .mob-menu__link {
    font-size: 48px;
  }
}

.mob-menu__link:hover {
  color: var(--secondary-text-color);
}

.mob-menu__iteam:not(:last-child) {
  margin-bottom: 40px;
}

@media only screen and (max-width: 480px) {
  .mob__button {
    background-color: var(--accent-color);
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    padding: 12px 34px;
    display: flex;
  }
}

.mob__cta-wrapper {
  justify-content: center;
  align-items: center;
  padding: 63px 84px;
  display: flex;
}

@media only screen and (min-width: 767px) {
  .mob__cta-wrapper {
    display: none;
  }
}

@media screen and (max-width: 480px) {
  .modal-window {
    padding-left: 8px;
    padding-right: 8px;
  }
}

.modal-window {
  z-index: 120;
  width: 280px;
  height: auto;
  background: var(--primary-text-color);
  border: 2px solid var(--border-color-modal);
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 8px;
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
}

.modal-window__btn-close {
  background: none;
  border: 0;
  padding: 0;
  line-height: 0;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-window__icon-close {
  width: 16px;
  height: 16px;
}

.modal-window__title {
  font-family: Seymour One;
  font-weight: 400;
  font-size: var(--modal-buy-font-size);
  text-align: center;
  color: var(--secondary-text-color);
  margin-bottom: 40px;
  padding-top: 24px;
  line-height: 1.5;
}

.modal-buy-now__list {
  flex-direction: column;
  gap: 20px;
  margin-bottom: 24px;
  display: flex;
}

.modal-buy-now__thumb {
  width: 132px;
  height: 200px;
  flex-direction: column;
  justify-content: flex-end;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

.modal-buy-now__label {
  width: 132px;
  height: 136px;
  border-radius: 10px;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.modal-buy-now__label_green {
  border: 2px solid var(--border-bg-color-label-green);
}

.modal-buy-now__check_green:checked + .modal-buy-now__label_green {
  background-color: var(--border-bg-color-label-green);
}

.modal-buy-now__label_yellow {
  border: 2px solid var(--border-bg-color-label-yellow);
}

.modal-buy-now__check_yellow:checked + .modal-buy-now__label_yellow {
  background-color: var(--border-bg-color-label-yellow);
}

.modal-buy-now__label_brown {
  border: 2px solid var(--border-bg-color-label-brown);
}

.modal-buy-now__check_brown:checked + .modal-buy-now__label_brown {
  background-color: var(--border-bg-color-label-brown);
}

.modal-buy-now__img {
  width: 132px;
  height: 132px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-buy-now__img_green {
  background: url("ice-creem.ae192eeb.png"), linear-gradient(0deg, var(--modal-bg-green-07), var(--modal-bg-green-07) ), #d9d9d9;
  box-shadow: 0px 6px 0px var(--modal-bg-green-1);
  border-radius: 50%;
}

.modal-buy-now__img_yellow {
  background: url("ice-coffee.7c0b0165.png"), linear-gradient(0deg, var(--modal-bg-yellow-07), var(--modal-bg-yellow-07) ), #d9d9d9;
  box-shadow: 0px 6px 0px var(--modal-bg-yellow-1);
}

.modal-buy-now__img_brown {
  background: url("milkshake.eb541f03.png"), linear-gradient(0deg, var(--modal-bg-brown-07), var(--modal-bg-brown-07) ), #d9d9d9;
  box-shadow: 0px 6px 0px var(--modal-bg-brown-1);
}

.modal-buy-now__text {
  font-family: Montserrat;
  font-weight: 700;
  font-size: var(--modal-buy-font-size);
  letter-spacing: var(--primary-letter-spacing);
  text-transform: uppercase;
  color: var(--secondary-text-color);
  padding-bottom: 20px;
  line-height: 1.25;
}

.modal-contacts {
  width: 224px;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin-bottom: 24px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.modal-contacts__user-name, .modal-contacts__phone, .modal-contacts__comments {
  width: 100%;
  height: 32px;
  background: var(--input-bg-color);
  border: 1px solid var(--border-color-input);
  border-radius: 10px;
}

.modal-contacts__comments {
  height: 64px;
}

.modal-contacts__user-name, .modal-contacts__phone, .modal-contacts__comments {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 400;
  font-size: var(--mobile-secondary-font-size);
  padding: 8px 12px;
  line-height: 1.41;
}

.modal-contacts__user-name::-ms-input-placeholder, .modal-contacts__phone::-ms-input-placeholder, .modal-contacts__comments::-ms-input-placeholder {
  color: var(--secondary-text-color);
}

.modal-contacts__user-name::placeholder, .modal-contacts__phone::placeholder, .modal-contacts__comments::placeholder {
  color: var(--secondary-text-color);
}

.modal-contacts__comments {
  resize: none;
}

.modal-contacts__user-name:not(:placeholder-shown):valid, .modal-contacts__phone:not(:placeholder-shown):valid {
  border-color: var(--valid-form-border-color);
}

.modal-contacts__user-name:not(:placeholder-shown):invalid, .modal-contacts__phone:not(:placeholder-shown):invalid {
  border-color: var(--invalid-form-border-color);
}

.modal-buy-now__btn {
  margin-bottom: 32px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@media screen and (min-width: 768px) {
  .modal-window {
    width: 568px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .modal-window__btn-close {
    top: 20px;
    right: 20px;
  }

  .modal-window__icon-close {
    width: 20px;
    height: 20px;
  }

  .modal-buy-now__list {
    flex-direction: row;
    justify-content: center;
    gap: 32px;
    margin-bottom: 40px;
  }

  .modal-contacts {
