.modal-ingredients__list {
  justify-content: center;
  align-items: center;
  gap: 9px;
  margin-bottom: 39px;
  display: flex;
}

.modal-ingredients__items {
  width: 53px;
  border-radius: 40px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal-ingredients__ice-cream-color {
  border: 2px solid #9cbe8f;
}

.modal-ingredients__ice-coffee-color {
  border: 2px solid #efd478;
}

.modal-ingredients__milkshakes-color {
  border: 2px solid #d6936d;
}

.modal-ingredients__weight {
  width: 40px;
